import React, { useEffect, useState } from "react";
import * as XLSX from "xlsx";
import axios from "axios";

const CLASS_OPTIONS = [
  "JHS 1", "JHS 2", "JHS 3",
    "Upper Primary 4", "Upper Primary 5", "Upper Primary 6",
      "Lower Primary 1", "Lower Primary 2", "Lower Primary 3",
      ];

      const BASE_URL = `process.env.REACT_APP_REGISTER_API/stu_record`;

      const Assessment = () => {
        const [selectedClass, setSelectedClass] = useState("");
          const [students, setStudents] = useState([]);
            const [selectedStudentId, setSelectedStudentId] = useState("");
              const [formData, setFormData] = useState({
                  id: "",
                      name: "",
                          weeks: Array(12).fill(""),
                              examRaw: "",
                                });
                                  const [studentEntries, setStudentEntries] = useState([]);

                                    useEffect(() => {
                                        if (selectedClass) {
                                              axios.get(`${BASE_URL}/students?class=${encodeURIComponent(selectedClass)}`)
                                                      .then(res => setStudents(res.data))
                                                              .catch(() => setStudents([]));
                                                                  }
                                                                    }, [selectedClass]);

                                                                      useEffect(() => {
                                                                          const selected = students.find(s => s.id === selectedStudentId);
                                                                              if (selected) {
                                                                                    setFormData(prev => ({
                                                                                            ...prev,
                                                                                                    id: selected.id,
                                                                                                            name: selected.name
                                                                                                                  }));
                                                                                                                      }
                                                                                                                        }, [selectedStudentId]);

                                                                                                                          const handleWeekChange = (index, value) => {
                                                                                                                              const newWeeks = [...formData.weeks];
                                                                                                                                  newWeeks[index] = value;
                                                                                                                                      setFormData({ ...formData, weeks: newWeeks });
                                                                                                                                        };

                                                                                                                                          const addEntry = () => {
                                                                                                                                              const weekScores = formData.weeks.map(Number);
                                                                                                                                                  const classRaw = weekScores.reduce((a, b) => a + b, 0);
                                                                                                                                                      const classScore50 = (classRaw / 120) * 50;
                                                                                                                                                          const examRaw = Number(formData.examRaw);
                                                                                                                                                              const examScore50 = (examRaw / 100) * 50;
                                                                                                                                                                  const total = classScore50 + examScore50;

                                                                                                                                                                      const entry = {
                                                                                                                                                                            "ID#": formData.id,
                                                                                                                                                                                  "STUDENT NAME": formData.name,
                                                                                                                                                                                        ...Object.fromEntries(weekScores.map((score, i) => [`Week ${i + 1}`, score])),
                                                                                                                                                                                              "CLASS RAW SCORE": classRaw,
                                                                                                                                                                                                    "CLASS  SCORE(50%)": classScore50.toFixed(2),
                                                                                                                                                                                                          "EXAM RAW SCORE": examRaw,
                                                                                                                                                                                                                "EXAM SCORE(50%)": examScore50.toFixed(2),
                                                                                                                                                                                                                      "TOTAL SCORE(100%)": total.toFixed(2),
                                                                                                                                                                                                                            "Position": "",
                                                                                                                                                                                                                                };

                                                                                                                                                                                                                                    setStudentEntries([...studentEntries, entry]);
                                                                                                                                                                                                                                        setFormData({ id: "", name: "", weeks: Array(12).fill(""), examRaw: "" });
                                                                                                                                                                                                                                            setSelectedStudentId("");
                                                                                                                                                                                                                                              };

                                                                                                                                                                                                                                                const exportToExcel = () => {
                                                                                                                                                                                                                                                    const worksheet = XLSX.utils.json_to_sheet(studentEntries);
                                                                                                                                                                                                                                                        const workbook = XLSX.utils.book_new();
                                                                                                                                                                                                                                                            XLSX.utils.book_append_sheet(workbook, worksheet, selectedClass || "Sheet");
                                                                                                                                                                                                                                                                XLSX.writeFile(workbook, `${selectedClass.replace(/\s+/g, "_")}_Assessment.xlsx`);
                                                                                                                                                                                                                                                                  };

                                                                                                                                                                                                                                                                    return (
                                                                                                                                                                                                                                                                        <div className="max-w-3xl mx-auto p-6 bg-white rounded shadow">
                                                                                                                                                                                                                                                                              <h2 className="text-2xl font-bold text-center text-blue-600 mb-4">Assessment Entry</h2>

                                                                                                                                                                                                                                                                                    {/* Class Selection */}
                                                                                                                                                                                                                                                                                          <div className="mb-4">
                                                                                                                                                                                                                                                                                                  <label className="block font-medium mb-1">Select Class</label>
                                                                                                                                                                                                                                                                                                          <select
                                                                                                                                                                                                                                                                                                                    className="w-full border p-2 rounded"
                                                                                                                                                                                                                                                                                                                              value={selectedClass}
                                                                                                                                                                                                                                                                                                                                        onChange={(e) => setSelectedClass(e.target.value)}
                                                                                                                                                                                                                                                                                                                                                >
                                                                                                                                                                                                                                                                                                                                                          <option value="">-- Choose Class --</option>
                                                                                                                                                                                                                                                                                                                                                                    {CLASS_OPTIONS.map(cls => (
                                                                                                                                                                                                                                                                                                                                                                                <option key={cls} value={cls}>{cls}</option>
                                                                                                                                                                                                                                                                                                                                                                                          ))}
                                                                                                                                                                                                                                                                                                                                                                                                  </select>
                                                                                                                                                                                                                                                                                                                                                                                                        </div>

                                                                                                                                                                                                                                                                                                                                                                                                              {/* Student Selection */}
                                                                                                                                                                                                                                                                                                                                                                                                                    {students.length > 0 && (
                                                                                                                                                                                                                                                                                                                                                                                                                            <div className="mb-4">
                                                                                                                                                                                                                                                                                                                                                                                                                                      <label className="block font-medium mb-1">Select Student</label>
                                                                                                                                                                                                                                                                                                                                                                                                                                                <select
                                                                                                                                                                                                                                                                                                                                                                                                                                                            className="w-full border p-2 rounded"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                        value={selectedStudentId}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    onChange={(e) => setSelectedStudentId(e.target.value)}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              >
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <option value="">-- Choose Student --</option>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      {students.map(stu => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <option key={stu.id} value={stu.id}>{stu.name}</option>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          </select>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        )}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              {/* Score Input */}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    {formData.name && (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            <>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      <div className="mb-4">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  <label className="block font-semibold">Weekly Scores</label>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              <div className="grid grid-cols-4 gap-2">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            {formData.weeks.map((score, i) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            <input
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              key={i}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                type="number"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  min={0}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    max={10}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      placeholder={`W${i + 1}`}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        value={score}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          className="border p-1 rounded text-center"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            onChange={(e) => handleWeekChange(i, e.target.value)}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            />
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </div>

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <div className="mb-4">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      <label className="block font-medium mb-1">Exam Raw Score</label>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  <input
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                type="number"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              className="w-full border p-2 rounded"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            value={formData.examRaw}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          onChange={(e) => setFormData({ ...formData, examRaw: e.target.value })}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      />
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </div>

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <button
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      onClick={addEntry}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            >
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        Add Student Entry
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  </button>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          </>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                )}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      {/* Export */}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            {studentEntries.length > 0 && (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <div className="mt-6">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              <button
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          onClick={exportToExcel}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                >
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            Export to Excel
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      </button>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                <ul className="mt-3 text-sm list-disc pl-6">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            {studentEntries.map((entry, i) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          <li key={i}>{entry["STUDENT NAME"]} - {entry["TOTAL SCORE(100%)"]}</li>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </ul>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              )}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    );
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    };

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    export default Assessment;